import React from "react";
import VistaTableContainer from "../VistaTableContainer/VistaTableContainer";
// import { VistaTable } from '../../lib';

import { VistaTable } from "vista-table-react";
import "./VistaTableDemo.css";
import * as SM from './data';

const VistaTableDemo = () => {

    return (
        <div>
            <div className="quotes-container">
                <div className="double-quotes">&#8220;</div>
                <div className="quotes">
                    <span style={{fontWeight:"bold"}}>Vista Table React</span> is React table component which can be setup very easily. One can simply use Vista table react and reduce the burden of writing lot many lines of javascript code to achieve table functionality.
                </div>
            </div>
            <br/>
            <hr/>
            <h4>Table Classic pagination</h4>
            <div style={{ width: "100%" }}>
                <VistaTableContainer header={SM.SIMPLE_HEADER} data={SM.SIMPLE_DATA} className="" config={{}} />
            </div>
            <br/>
            <hr/>
            <h4>Table Classic pagination</h4>
            <div style={{ width: "100%" }}>
                <VistaTableContainer header={SM.SAMPLE_HEADER} data={SM.SAMPLE_DATA} className="classic" config={SM.CONFIG_SORT} />
            </div>
            <br/>
            <hr/>
            <h4>Table Classic no pagination</h4>
            <div style={{ width: "100%" }}>
                <VistaTableContainer header={SM.SAMPLE_HEADER} data={SM.SAMPLE_DATA} className="dark" config={SM.CONFIG} />
            </div>
            <br/>
            <hr/>
            <h4>Basic table</h4>
            <div style={{ width: "100%" }}>
                <VistaTableContainer header={SM.SAMPLE_HEADER} data={SM.SAMPLE_DATA} config={SM.CONFIG} />
            </div>
            <div className="floating-badge">
                <a href="https://codesandbox.io/s/vista-table-react-no-filter-pagination-forked-69fp1" target="_blank">
                    <span >P</span>
                    <span>l</span>
                    <span>a</span>
                    <span>y</span>
                    <span>&nbsp;</span>
                    <span>g</span>
                    <span>r</span>
                    <span>o</span>
                    <span>u</span>
                    <span>n</span>
                    <span>d</span>
                </a>
            </div>
        </div>
    )
}
export default VistaTableDemo;