import React, { useEffect, useState } from "react";
// import { VistaTable } from '../../lib';
import { VistaTable } from "vista-table-react";
import "./VistaTableContainer.css";

const VistaTableContainer = ({
    header,
    data,
    config
}) => {
    const [tableConfig, setTableConfig] = useState(config);
    const [codeView, setCodeView] = useState(false);
    const [theme, setTheme] = useState("");
    const [sorting, setSorting] = useState(config?.sorting);
    const [pagination, setPagination] = useState(config?.pagination);
    const [filter, setFilter] = useState(config?.filter);

    useEffect(() => {
        setTableConfig({...config, pagination:pagination, sorting:sorting, filter:filter});
    }, [pagination,theme, sorting, filter]);
    return (
        <div className={" vista-container"}>
            <div className="config-container">
                <div>
                    <label>Theme: </label>
                    <select value={theme} className="select-style" onChange={(e) => setTheme(e.target.value)}>
                        <option value="">Default</option>
                        <option value="dark">Dark</option>
                        <option value="classic">Classic</option>
                    </select>
                </div>
                <div>
                    <label className="container">Pagination
                        <input defaultChecked={pagination} type="checkbox" onChange={(e) => setPagination(e.target.checked)}/>
                        <span className="checkmark"></span>
                    </label>
                </div>
                <div>
                    <label className="container">Filter
                        <input defaultChecked={filter} type="checkbox" onChange={(e) => setFilter(e.target.checked)}/>
                        <span className="checkmark"></span>
                    </label>
                </div>
                <div>
                    <label className="container">Sorting
                        <input defaultChecked={sorting} type="checkbox" onChange={(e) => setSorting(e.target.checked)}/>
                        <span className="checkmark"></span>
                    </label>
                </div>
            </div>
            <div className="tab-container">
                <div className={"tabs" + (!codeView? " first":"")} onClick={() =>setCodeView(false)}>Preview</div>
                <div className={"tabs" + (codeView? " second":"")} onClick={() =>setCodeView(true)}>Code</div>
            </div>
            {codeView && 
                <div className="code-container">
                    <pre className="code-block">
                        <code>{"\n"}
                            &lt;VistaTable headers={"{headers}"} data={"{data}"} className={"{theme}"} config={"{config}"} /&gt;
                            {"\n"}
                            {"\n"}
                            const theme = {"\""}{theme}{"\""};{"\n"}
                            const config = <pre className="tab-space">{JSON.stringify(tableConfig, null, 1) }</pre>
                            const headers = {"\t"} <pre className="tab-space">{JSON.stringify(header, null, 1) }</pre>
                            const data = <pre className="tab-space">{JSON.stringify(data, null, 1) }</pre>
                            
                        </code>
                    </pre>
                </div>}
            {!codeView &&
                <div>
                    <VistaTable headers={header} data={data} className={theme} config={tableConfig} />
                </div>}
        </div>
    );
}
export default VistaTableContainer;